import React, { useMemo } from "react";
import styled from "styled-components";
import { Tooltip, IconButton } from "@material-ui/core";
import AllInclusiveIcon from "@material-ui/icons/AllInclusive";
import BlurCircularIcon from "@material-ui/icons/BlurCircular";
import MergeTypeIcon from "@material-ui/icons/MergeType";

const iconMap = {
  0: BlurCircularIcon,
  1: AllInclusiveIcon,
  2: MergeTypeIcon,
};

export const Container = styled.div`
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
`;
export const StyledIconButton = styled(IconButton)`
  box-shadow: ${(props) =>
    props.enabled
      ? "inset -4px -4px 8px rgba(255, 255, 255, 0.6), inset 8px 8px 16px rgba(0, 0, 0, 0.2)"
      : "-6px -6px 10px rgba(255, 255, 255, 0.7), 6px 6px 10px rgba(0, 0, 0, 0.3)"};
  color: black !important;
  background-color: white !important;
`;

export default function AnimationButton({ onClick, configIndex }) {
  const Icon = useMemo(() => iconMap[configIndex], [configIndex]);
  return (
    <Container>
      <Tooltip title="Change animation">
        <StyledIconButton onClick={onClick}>
          <Icon fontSize="large" />
        </StyledIconButton>
      </Tooltip>
    </Container>
  );
}
