import React from "react";
import styled from "styled-components";
import { Tooltip, IconButton } from "@material-ui/core";

export const Container = styled.div`
  position: fixed;
  top: 0;
  left: 10px;
`;
export const StyledIconButton = styled(IconButton)`
  box-shadow: ${(props) =>
    props.enabled
      ? "inset -4px -4px 8px rgba(255, 255, 255, 0.6), inset 8px 8px 16px rgba(0, 0, 0, 0.2)"
      : "-6px -6px 10px rgba(255, 255, 255, 0.7), 6px 6px 10px rgba(0, 0, 0, 0.3)"};
  color: black !important;
  background-color: white !important;
  height: 55px;
  width: 55px;
`;
const Text = styled.div`
  font-weight: 800;
  font-size: 1.1rem;
`;

export default function QualityButton({ quality, setQuality }) {
  return (
    <Container>
      <Tooltip title="Change quality">
        <StyledIconButton onClick={() => setQuality(null)}>
          <Text>{quality}</Text>
        </StyledIconButton>
      </Tooltip>
    </Container>
  );
}
