import { Plane } from "@react-three/drei";

export default function Floor() {
  return (
    <Plane
      rotation={[-Math.PI / 2, 0, 0]}
      position={[0, -300, 0]}
      args={[10000, 10000]}
    >
      <meshStandardMaterial attach="material" color="darkgrey" />
    </Plane>
  );
}
