import {
  QSBackground,
  QSContainer,
  QSTitle,
  QSButtonsContainer,
  QSButton,
} from "./QualitySelectStyle";
export default function QualitySelect({ setQuality }) {
  const onClick = (value) => {
    setQuality(value);
    document.body.style.cursor = "wait";
  };
  return (
    <QSBackground>
      <QSContainer>
        <QSTitle>Select Quality</QSTitle>
        <QSButtonsContainer>
          <QSButton onClick={() => onClick("LQ")}>Low</QSButton>
          <QSButton onClick={() => onClick("MQ")}>Medium</QSButton>
          <QSButton onClick={() => onClick("HQ")}>High</QSButton>
        </QSButtonsContainer>
      </QSContainer>
    </QSBackground>
  );
}
