import styled from "styled-components";
const topShadow = "#cbced1";
const bottomShadow = "#ffffff";

export const QSBackground = styled.div`
  height: 100vh;
  width: 100vw;
  background-color: #ecf0f3;

  display: flex;
  align-items: center;
  justify-content: center;
`;
export const QSContainer = styled.div`
  box-sizing: border-box;
  height: 150;
  width: 350px;
  padding: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #ecf0f3;
  border-radius: 40px;
  box-shadow: 13px 13px 20px ${topShadow}, -13px -13px 20px ${bottomShadow};
`;
export const QSTitle = styled.div`
  font-size: 1.6rem;
  font-weight: 500;
  color: rgb(10, 144, 197);
`;
export const QSButtonsContainer = styled.div`
  display: flex;
  margin-top: 15px;
`;
export const QSButton = styled.div`
  box-sizing: border-box;
  background-color: rgb(10, 144, 197);
  margin: 10px;
  padding: 15px 40px;
  border-radius: 20px;
  width: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
`;
