import { Suspense, useState, lazy, useRef } from "react";
import { Canvas } from "@react-three/fiber";
import { OrbitControls, Loader } from "@react-three/drei";
import Floor from "./Components/Floor";
import Rig from "./Components/Rig";
import ScrollDown from "./Components/ScrollDown/ScrollDown";
import { configs } from "./configs";
import { qualityCfgs } from "./qualityCfgs";
import AnimationButton from "./Components/AnimationButton/AnimationButton";
import QualityButton from "./Components/QualityButton/QualityButton";
const Model = lazy(() => import("./Components/Model/Model"));

export default function App({ quality, setQuality }) {
  const mouse = useRef([0, 0]);
  const loading = useRef(false);
  const [configIndex, setConfigIndex] = useState(0);
  const config = useRef(configs[0]);
  const qualityCfg = useRef(qualityCfgs[quality]);
  const tempConfig = useRef(null);
  const [nextModel, setNextModel] = useState(false);

  const onNextModel = () => {
    if (loading.current) return;
    loading.current = true;
    document.body.style.cursor = "wait";
    setNextModel(Date.now());
  };

  const onMouseMove = (e) => {
    mouse.current = [
      2500 * (e.clientX / window.innerWidth - 0.5),
      1500 * (e.clientY / window.innerHeight - 0.5),
    ];
  };
  const onChangeTransition = () => {
    setConfigIndex((prev) => {
      const nextIndex = prev < configs.length - 1 ? prev + 1 : 0;
      if (!loading.current) config.current = configs[nextIndex];
      else tempConfig.current = configs[nextIndex];
      return nextIndex;
    });
  };

  return (
    <div
      className="container"
      onWheel={onNextModel}
      onPointerMove={onMouseMove}
    >
      <Canvas
        colorManagement
        camera={{
          position: [0, qualityCfg.current.cameraY, qualityCfg.current.cameraZ],
          fov: 40,
          far: 10000,
        }}
      >
        <fog attach="fog" args={["white", 0, 5000]} />
        <ambientLight color="#ffffff" intensity={0.5} />
        <directionalLight intensity={0.5} position={[-300, 600, 250]} />

        <Suspense fallback={null}>
          <Model
            loading={loading}
            nextModel={nextModel}
            config={config}
            tempConfig={tempConfig}
            qualityCfg={qualityCfg}
          />
        </Suspense>
        {/* <OrbitControls target={[0, 0, 0]} /> */}
        <Rig mouse={mouse} mouseRange={qualityCfg.current.mouseRange} />
        <Floor />
      </Canvas>
      <Loader />
      <ScrollDown />
      <AnimationButton onClick={onChangeTransition} configIndex={configIndex} />
      <QualityButton
        quality={qualityCfg.current.filePath}
        setQuality={setQuality}
      />
    </div>
  );
}
